











































.tab{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    width: 100%;
    height: 40px;
    line-height: 40px;
    /*border-bottom: solid 1px rgba(7,17,27,0.1);*/
}
.tab::after{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    border-top: solid 1px rgba(7,17,27,0.1);
    content: '';
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
#app .tab .tab-item{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: center;
}
#app .tab .tab-item a{
    display: block;
    font-size: 14px;
    color: rgb(77,85,93);
}
#app .tab .tab-item .active{
    color: rgb(240,20,20);
}



