
















































































.shopcart{
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 50;
    width: 100%;
    height: 48px;
}
.shopcart .content{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #141b27;
    font-size: 0;
}
.shopcart .content .content-left{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.shopcart .content .content-left .logo-wrapper{
    display: inline-block;
    position: relative;
    top: -10px;
    margin: 0 12px;
    padding: 6px;
    width: 56px;
    height: 56px;
    box-sizing: border-box;
    vertical-align: top;
    border-radius: 50%;
    background: #141b27;
}
.shopcart .content .content-left .logo-wrapper .logo{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background: #2b343c;
    text-align: center;
}
.shopcart .content .content-left .logo-wrapper .highlight{
    background: rgb(0,160,220);
}
.shopcart .content .content-left .logo-wrapper .logo .icon-shopping_cart{
    font-size: 24px;
    color: #80858a;
    line-height: 44px;
}
.shopcart .content .content-left .logo-wrapper .highlight .icon-shopping_cart{
    color: #fff;
}
.shopcart .content .content-left .logo-wrapper .num{
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 16px;
    line-height: 16px;
    text-align: center;
    border-radius: 16px;
    font-size: 9px;
    font-weight: 700;
    color: #fff;
    background-color: rgb(240,20,20);
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
}
.shopcart .content .content-left .price{
    display: inline-block;
    vertical-align: top;
    line-height: 24px;
    margin-top: 12px;
    padding-right: 12px;
    box-sizing: border-box;
    border-right: 1px solid rgba(255,255,255,0.1);
    font-size: 16px;
    font-weight: 700;
    color: rgba(255,255,255,0.4);
}
.shopcart .content .content-left .highlight{
    color: #fff;
}
.shopcart .content .content-left .desc{
    display: inline-block;
    vertical-align: top;
    line-height: 24px;
    margin: 12px 0 0 12px;
    font-size: 10px;
    color: rgba(255,255,255,0.4);
}
.shopcart .content .content-right{
    -webkit-box-flex: 0;
        -ms-flex: 0 0 105px;
            flex: 0 0 105px;
    width: 105px;
}
.shopcart .content .content-right .pay{
    height: 48px;
    line-height: 48px;
    text-align: center;
    font-size: 12px;
    color: rgba(255,255,255,0.4);
    font-weight: 700;
    background: #2b333b;
}
.shopcart .content .content-right .enough{
    background: #00b43c;
    color: #fff;
}



