












































.cartcontrol .cart-decrease,.cartcontrol .cart-add{
    display: inline-block;
    width: 20px;
    height: 20px;
    line-height: 18px;
    text-align: center;
    border-radius: 50%;
    background: red;
    color: #fff;
    font-weight: 700;
}
.cartcontrol .cart-decrease{
    transition: all 0.4s linear;
}
.cartcontrol .move-transition{
    opacity: 1;
    -webkit-transform: translate3D(0,0,0);
            transform: translate3D(0,0,0);
}
.cartcontrol .move-transition .inner{
    display: inline-block;
    transition: all 0.4s linear;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
.cartcontrol .move-enter,.cartcontrol .move-leave{
    opacity: 0;
    -webkit-transform: translate3D(24px,0,0);
            transform: translate3D(24px,0,0);
}
.cartcontrol .move-enter .inner,.cartcontrol .move-leave .inner{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
.cartcontrol .cart-count{
    display: inline-block;
}



